.loose {
  opacity: 0.3;
}

.answer-button {
  width: 40%;
  height: 3vw;
  color: wheat;
  font-size: 1.1vw;
  font-weight: bold;
  border-radius: 20px;
  margin: 3vh 30px;
  background: linear-gradient(121.83deg, #774cd3 31.32%, #ae2aa9 87.66%);
}

.answer-button:hover {
  cursor: pointer;
}

.chosenBtn {
  border: 5px solid black;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-500px, 200px, 0) rotate(180deg);
  }

  20%,
  80% {
    transform: translate3d(600px, -150px, 0) rotate(80deg);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-100px, -150px, 0) rotate(260deg);
  }

  40%,
  60% {
    transform: translate3d(100px, 200px, 0) rotate(180deg);
  }
}

.hard-difficulty {
  animation-name: shake;
  animation-duration: 15s;
  animation-iteration-count: infinite;
}
